@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@500&family=Source+Code+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
    font-family: Panton;
    src: url(/personal-website/static/media/Panton-BlackCaps.63054592.otf);
}

.App {
    text-align: center;
}

.App-header {
    background-color: #fff;
    min-height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-columns: 150px calc(100vw - 150px);
    color: white;
}
nav {
    background: #FFFFFF;
    border-radius: 0px 20px 20px 0px;
    margin: 20px 0 20px 0;
    z-index: 1;
}

nav h1 {
    position: relative;
    top: 10vh;
    left: 0;
    white-space: nowrap;
    font-family: Panton;
    font-size: 48px;
    color: #000;
    transform: rotate(90deg);
}
.main_background__1o66f {
    margin: 20px 20px 20px -7vh;
    border-radius: 20px;
    background: #171717 url(/personal-website/static/media/bg.da4b1582.jpg) no-repeat fixed right;
    background-clip: border-box;
    z-index: 0;
}

.main_titleText__28Tfr h2 {
    font-family: 'Montserrat Alternates', sans-serif;
    font-size: 42px;
    font-weight: 400;
    margin: 0 0 -10px 0;
}

.main_titleText__28Tfr p {
    font-family: 'Source Code Pro', monospace;
    font-size: 18px;
    margin: 18px 0 30px 0;
    animation: main_seconds__2aFu9 1.0s forwards;
    animation-iteration-count: 1;
    animation-delay: 1.35s;
    opacity: 0
}

@keyframes main_seconds__2aFu9 {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 1;
    }
}

.main_App-header__3Qi43 div {
    font-family: 'Montserrat Alternates', sans-serif;
    font-size: 14px;
}

.main_titleBox__2GLCZ {
    margin: 125px 55vw 0 calc(7vh + 90px);
    text-align: left;
}

.main_permanent__3hL4I {
    border-right: .05em solid;
    animation: main_caret-p__1SnKi 1s steps(1) infinite;
}

.main_temporary__307NM {
    border-right: .05em solid;
    animation: main_caret-h2__2WFc1 1s steps(1) infinite;
    animation-delay: 0.7s;
    opacity: 1;
}

@keyframes main_caret-h2__2WFc1 {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 0;
    }
}

@keyframes main_caret-p__1SnKi {
    50% {
        border-color: transparent;
    }
}
.email_button__3PYfv {
    width: 10rem;
    height: 2.5rem;
    border-style: solid;
    border-width: 2px;
    border-color: white;
    position: relative;
}

.email_button__3PYfv a {
    font-family: Montserrat Alternates;
    font-weight: 500;
    font-size: 14px;
    text-decoration: none;
    color: #ffffff;
    white-space: nowrap;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}
